/* src/components/Projects.css */
.projects {
    background-color: #101010;
    color: white;
  }
  
  .projects h2 {
    font-size: 2rem;
    margin-bottom: 30px;
  }
  
  .project-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
  }
  
  .project-card {
    background-color: #1f1f1f;
    padding: 20px;
    width: 300px;
    border-radius: 10px;
    text-align: left;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
  }
  
  .project-card h3 {
    margin-bottom: 10px;
  }
  
  .project-card .tags {
    margin: 15px 0;
  }
  
  .tag {
    display: inline-block;
    background-color: #2a2a2a;
    padding: 5px 10px;
    margin: 2px;
    border-radius: 4px;
    font-size: 0.9rem;
  }
  
  .project-card button {
    background-color: #00ffe0;
    color: black;
    border: none;
    padding: 8px 20px;
    border-radius: 5px;
  }
  
  .project-card button:hover {
    background-color: #00ccaa;
  }
  