/* BASE */
body {
  margin: 0;
  padding: 0;
  background: #121212;
  color: white;
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
}

section {
  padding: 60px 20px;
  max-width: 1100px;
  margin: 0 auto;
}

/* HEADINGS */
h1 {
  font-size: 3rem;
  margin-bottom: 10px;
}

h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

h3 {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

/* INTRO & ABOUT */
.intro p, .about p {
  font-size: 1.1rem;
}

/* PROJECTS */
.project {
  background: #1e1e1e;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}

/* SKILLS GRID */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.skills-grid ul {
  list-style: none;
  padding-left: 0;
}

.skills-grid li {
  margin-bottom: 6px;
}

/* CAPABILITIES */
.capabilities ul {
  padding-left: 20px;
}

.capabilities li {
  margin-bottom: 8px;
}

/* CONTACT FORM */
form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 500px;
  margin: auto;
}

input, textarea {
  padding: 14px;
  border-radius: 5px;
  border: none;
  background-color: #2a2a2a;
  color: white;
  font-size: 16px;
  width: 100%;
  box-sizing: border-box;
}

textarea {
  resize: vertical;
  min-height: 120px;
}

button {
  padding: 14px;
  background: #00ffe0;
  border: none;
  color: black;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background: #00ccaa;
}

/* RESPONSIVENESS */
@media (max-width: 768px) {
  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  h3 {
    font-size: 1.1rem;
  }

  section {
    padding: 40px 15px;
  }

  .skills-grid {
    grid-template-columns: 1fr;
  }

  form {
    padding: 0 10px;
  }

  input, textarea, button {
    font-size: 15px;
  }
}
