/* src/components/Hero.css */
.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #0f0f0f;
    color: white;
  }
  
  .hero h1 {
    font-size: 3rem;
    margin: 10px 0;
  }
  
  .hero p {
    font-size: 1.2rem;
    margin: 20px 0;
    max-width: 500px;
    text-align: center;
  }
  
  .hero button {
    background-color: #00ffe0;
    color: black;
    border: none;
    padding: 10px 25px;
    font-size: 1rem;
    border-radius: 5px;
    transition: background 0.3s;
  }
  
  .hero button:hover {
    background-color: #00ccaa;
  }
  