/* src/components/Contact.css */
.contact {
    background-color: #101010;
    color: white;
  }
  
  .contact h2 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  form {
    max-width: 500px;
    margin: auto;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  input, textarea {
    padding: 12px;
    border: none;
    border-radius: 5px;
    background: #1f1f1f;
    color: white;
  }
  
  textarea {
    resize: vertical;
    min-height: 120px;
  }
  
  button {
    background-color: #00ffe0;
    color: black;
    border: none;
    padding: 12px;
    border-radius: 5px;
  }
  
  button:hover {
    background-color: #00ccaa;
  }
  