/* Navbar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #181818;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .logo {
    font-size: 1.5rem;
    font-weight: bold;
    color: #00ffe0;
  }
  
  .nav-links {
    display: flex;
    gap: 25px;
    list-style: none;
  }
  
  .nav-links li a {
    color: white;
    text-decoration: none;
    font-size: 1rem;
    transition: color 0.2s ease-in-out;
  }
  
  .nav-links li a:hover {
    color: #00ffe0;
  }
  
  /* MOBILE RESPONSIVENESS */
  @media (max-width: 768px) {
    .nav-links {
      flex-direction: column;
      gap: 15px;
      position: absolute;
      top: 70px;
      right: 20px;
      background-color: #181818;
      padding: 20px;
      border-radius: 10px;
      display: none;
    }
  
    .nav-links.active {
      display: flex;
    }
  
    .navbar {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .menu-toggle {
      display: block;
      color: white;
      cursor: pointer;
      font-size: 1.5rem;
    }
  }
  