/* src/components/Skills.css */
.skills {
    background-color: #181818;
    color: white;
  }
  
  .skills h2 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .skill-tags {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 15px;
  }
  
  .skill-tag {
    background-color: #292929;
    padding: 10px 20px;
    border-radius: 30px;
    font-size: 1rem;
  }

  